.aside-wrapper {
  background-color: #2b2f3e;
  min-height: 100vh;
  height: 100%;
  max-width: 600px;
  width: 100%;
  margin: 0 15px;
}

.aside-content {
  padding: 10px;
}

.aside-title {
  color: #ffff;
  font-size: 32px;
  font-weight: 500;
  line-height: 1.2;
  text-align: center;
  margin-bottom: 8px;

}

.aside-text {
  color: rgb(91, 93, 99);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  text-align: center;
}