.container {
  min-width: 500px;
  width: 100%;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
  padding: 10px;
  margin: 10px;
}

.items-container {
  height: 170px;
}

.items-form {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.items-input {
  margin: 0 10px;
  display: block;
  max-width: 362px;
  width: 100%;
  height: 24px;
  padding: 6px 12px;
  font-size: 16px;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.title {
  font-size: 40px;
  text-align: left;
  line-height: 1.2;
  font-weight: normal;
  color: gray;
  margin-bottom: 8px;


}


.items-button {
  background-color: rgb(23, 162, 184);
  border-radius: 4px;
  border: 1px solid transparent;
  padding: 6px 12px;
  color: #ffff;
  cursor: pointer;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";

}

.items-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style: none;
  padding: 12px 20px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .125);
  border-radius: 4px;

}

.items-box {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.items-name {
  font-size: 16px;
}

.items-num {
  color: #ffff;
  background-color: #17a2b8;
  font-size: 12px;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  display: inline-block;
  padding: 4px 8px;
  border-radius: 160px;
  margin-right: 15px;

}

.items-delete {
  color: #dc3545;
  background-color: #fff;
  display: inline-block;
  font-weight: 400;
  text-align: center;
  border: 1px solid #dc3545;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.items-delete:hover {
  background-color: #dc3545;
  color: #ffff;
}


input:focus {
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, .25)
}